.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  font-size: 0.8em; 
  padding: 15px; 
  border: 1px solid #888;
  width: 30%;
  max-width: 500px; 
  height: auto;  
  border-radius: 8px;
  box-sizing: border-box;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.close {
  
  
  color: #000000;
  font-size: 24px; 
  font-weight: bold;
  align-self: flex-end; 
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

label {
  display: block;
  margin: 10px 0 5px; 
  width: 100%;
}

input[type="text"],
input[type="tel"],
input[type="number"],
select {
  width: 100%; 
  padding: 10px; 
  margin: 5px 0 10px; 
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button[type="submit"] {
  width: 100%;
  background-color: #bfff31;
  color: rgb(0, 0, 0);
  padding: 12px; 
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #b4f32b;
}
